<template>
  <div>
    <b-spinner
      v-if="loading"
      style="width: 100px; height: 100px; position: fixed; top: 40%; left: 50%;"
      variant="secondary"
    ></b-spinner>
    <b-container v-if="!loading">
      <b-row class="justify-content-md-center pt-2">
        <b-col cols="5">
          <b-card-group deck>
            <b-card id bg-variant="light" class="shadow p-3 mb-5 rounded">
              <p>{{currentQuestion}}</p>
              <b-form-input
                size="sm"
                class="mr-sm-2"
                placeholder="secret code"
                type="text"
                v-model="search"
              ></b-form-input>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </b-container>
    <b-modal v-model="errored" centered hide-footer title="No Bueno..." header-bg-variant="danger" header-text-variant="dark">
      <p>{{errorMessage}}</p>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import { serverUrl } from "../config.js";
export default {
  name: "Quiz",
  data() {
    return {
      loading: true,
      errored: false,
      errorMessage: '',
      questions: [],
      currentStep: 0
    };
  },
  computed: {
    currentQuestion() {
      return this.questions[this.currentStep].questionText
    },
  },
  methods: {
    handleError(errorMsg, errorObject) {
      const message = errorMsg ? errorMsg : errorObject;
      console.log(message);
      this.errorMessage = message;
      this.errored = true;
      if(typeof errorObject === 'object' && errorObject.response.status === 403) {
        window.location.href = "login";
      }
    },
  },
  mounted() {
    axios
      .get(serverUrl + "/api/questions")
      .then(response => {
        this.questions = response.data;
      })
      .catch(error => {
        this.loading = false;
        this.handleError(null, error);
      })
      .finally(() => (this.loading = false));
  }
};
</script>

<style>
  .hidden_header {
    display: none;
  }

  .centered_header {
    text-align: center;
  }

  .redMessage {
    color: red;
  }


</style>



